nav{
    position: absolute;
    width: 5%;
    height: 110%;
    padding: 10px;
    top: 0%;
    bottom: 0%;

    background: #F6F7F7;

    /* Sombra Barra */

    box-shadow: 0px 10px 19px rgba(178, 179, 181, 0.7);
}

nav ul li img{
    width: 112%;
}

nav ul{
    margin-top: 30px;
    margin-left: -10px;
    list-style: none;
}

nav ul li{
    cursor: pointer;
}

.logo{
    margin-left: -5px;
}

.foto{
    margin-top: 70px;
    width: 70%;
    margin-left: 15px;
}




